import React, { useRef } from "react";
import { Button } from 'antd';
import {
  Wrap,
  Content,
  HeaderWrap,
  Logo,
  IconWrap,
  IconItem,
  IconImg,
  IconText,
  LoginWrap,
  Title,
  InputWrap,
} from "./styled";

import { LoginInput } from "./LoginInput";



export default () => {

  const inputData = useRef({
    username: '',
    password: ''
  })

  const iconArr = [
    {
      icon: require('./assets/icon_my.png'),
      text: '关于我们',
      onClick: () => {

      },
      color: '#1888AC'
    },
    // {
    //   icon: require('./assets/icon_phone.png'),
    //   text: '联系我们',
    //   onClick: () => {

    //   },
    //   color: '#EF8436'

    // },
    {
      icon: require('./assets/icon_arrow.png'),
      text: '登陆账号',
      onClick: () => {

      },
      color: '#188AAE'

    }
  ];

  const inputArr = [
    {
      label: '电话',
      maxLength:11,
      initValue: inputData.current.username,
      placeholder: 'username',
      onChange: (value: string) => {
        inputData.current.username = value;
      }
    },
    {
      label: '密码',
      type:'password',
      initValue: inputData.current.password,
      placeholder: 'password',
      onChange: (value: string) => {
        inputData.current.password = value;
      }
    }
  ]


  return (
    <Wrap>
      <HeaderWrap>
        <Logo>
          <img src={require('./assets/logo.png')} alt="" />
        </Logo>
        <IconWrap>
          {
            iconArr.map((v, i) => (
              <IconItem onClick={v.onClick} key={i}>
                <IconImg>
                  <img src={v.icon} alt="" />
                </IconImg>
                <IconText style={{ color: v.color }}>
                  {
                    v.text
                  }
                </IconText>
              </IconItem>
            ))
          }
        </IconWrap>
      </HeaderWrap>

      <Content>
        <LoginWrap>
          <Title>Welcome</Title>
          <InputWrap>
            {
              inputArr.map((v, i) => (
                <LoginInput key={i} {...v} />
              ))
            }
          </InputWrap>
          <Button>立即登陆</Button>
        </LoginWrap>
      </Content>

    </Wrap>
  )
};
